.container {
  min-width: 320px;
  padding-left: 15px;
  padding-right: 15px;
  @media screen and (min-width: 320px) {
    max-width: 350px;
  }
  @media screen and (min-width: 768px) {
    max-width: 695px;
    margin-left: auto;
    margin-right: auto;
  }

  @media screen and (min-width: 1280px) {
    max-width: 884px;

  }
}
